import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {ITransferServiceProvider} from "../../providers/i-transfer-service/i-transfer-service";
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";
import {FundTransferOtherBankProvider} from "../../providers/fund-transfer-other-bank/fund-transfer-other-bank";


@IonicPage()
@Component({
  selector: 'page-fund-transer-other-bank01',
  templateUrl: 'fund-transer-other-bank01.html',
})
export class fundTransferOtherBank01 {
  otherBankTransferError01;
  accounts = [];
  transactional;
  sourceAcc;
  accountDetails;
  operationType=0;
  header;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public tokenService: TokenServiceProvider,
              public storage: LocalStorageProvider,
              public maskTokenUtil: MaskTokenProvider,
              public cryptoUtil: CryptoParamUtilProvider,
              public transferService: ITransferServiceProvider,
              public ftOtherBankService: FundTransferOtherBankProvider,
              public gaProvider: GoogleAnalyticsProvider) {

    let dAccount = this.storage.retrieveFromStorage('accounts');
    if (dAccount == null || dAccount == "") {
      this.accounts = [];
    }
    else {

      dAccount = cryptoUtil.getDecryptVal(dAccount);
      dAccount = JSON.parse(dAccount);
      Object.keys(dAccount).forEach(key => {
        let value = dAccount[key];
        if (value.debitApproved == true) {
          this.accounts.push(value);
        }
      });
    }


    let dWelCome = this.storage.retrieveFromStorage('welcomeVal');
    if (dWelCome == null || dWelCome == "") {
    }
    else {
      dWelCome = cryptoUtil.getDecryptVal(dWelCome);
      dWelCome = JSON.parse(dWelCome);
      this.transactional = dWelCome.transactional;
    }
  }

  iTransferOtherBank01Action = function (account) {
    this.otherBankTransferError01 = "";

    if (account.debitApproved == false) {
      this.otherBankTransferError01 = "This Account is not permitted for iRecharge.";
      return;
    }

    if (this.transactional == 1) {
      this.sourceAcc = account.accountNo;
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }
    else {
      this.navCtrl.push('TxErrorPage');
    }
  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let encAccNo = this.cryptoUtil.getEncryptVal(this.sourceAcc);
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.transferService.fetchDestAndPurposeList(unMaskToken, appData.userId, appData.appId, encAccNo)
        .then(data => {
          this.onFetchDestAndPurposeListComplete(data);
        });
    }
  };

  onFetchDestAndPurposeListComplete = function (data) {
    if (data.statusCode == 1) {
      this.accountDetails = data.object.accInfoBean;

      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.ftOtherBankService.fetchTransferTypeList(unMaskToken, appData.userId, appData.appId)
        .then(data => {
          this.onFetchFundTransferListTypeComplete(data);
        });
    }

    else if (data.statusCode == 0) {
      this.otherBankTransferError01 = data.note;
    }

    else {
      this.otherBankTransferError01 = "System Error Contact with Administrator.";
    }
  };

  onFetchFundTransferListTypeComplete = function (data) {
    if (data.statusCode == 1) {
      this.navCtrl.push('FundTranserOtherBank02Page', {
        'accountNo': this.sourceAcc,
        'transferTypes': JSON.stringify(data.object.ftObServices),
        'ftOBDestType' : JSON.stringify(data.object.ftObServicesDestTypes) ,
        'accountDetails': JSON.stringify(this.accountDetails)
      });
    }

    else if (data.statusCode == 0) {
      this.otherBankTransferError01 = data.note;
    }

    else {
      this.otherBankTransferError01 = "System Error Contact with Administrator.";
    }
  };

  goToTransferHistoryAction = function () {
    this.navCtrl.push('FundTransferOtherBankHistory01Page');
  };

  goToAddOBCreditAccountAction = function () {
    this.operationType=1;
    this.header="Add Beneficiary";
    let appData = this.storage.retrieveFromStorage("appData");
    let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
    this.tokenService.getNewToken(unMaskRefToken)
      .then(data => {
        this.onNewTokenGenCompleteForManageAcc(data);
      });
  };

  goToDeleteOBCreditAccountAction = function () {
    this.operationType=2;
    this.header="Delete Beneficiary";
    let appData = this.storage.retrieveFromStorage("appData");
    let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
    this.tokenService.getNewToken(unMaskRefToken)
      .then(data => {
        this.onNewTokenGenCompleteForManageAcc(data);
      });
  };

  onNewTokenGenCompleteForManageAcc = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.ftOtherBankService.fetchTransferTypeList(unMaskToken, appData.userId, appData.appId)
        .then(data => {
          this.onFetchDestAndPurposeListManageAccountComplete(data);
        });
    }
  };

  onFetchDestAndPurposeListManageAccountComplete = function (data) {
    if (data.statusCode == 1) {
      this.navCtrl.push('ManageObCreditAccount01Page',{'operationType' : this.operationType,
        "header" :this.header,
        'ftOBDestType' : JSON.stringify(data.object.ftObServicesDestTypes) ,
        'transferTypes': JSON.stringify(data.object.ftObServices)});
    }

    else if (data.statusCode == 0) {
      this.otherBankTransferError01 = data.note;
    }

    else {
      this.otherBankTransferError01 = "System Error Contact with Administrator.";
    }
  };




  ionViewDidLoad() {
    // console.log('ionViewDidLoad ITransfer01Page');
    this.gaProvider.startTrackerWithId("iTransfer - Other Bank");
  }

}



